.friendList {
  .item {
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding: 10px 20px;

    @media screen and (min-width: 320px) and (max-width: 420px) {
      max-width: 320px;
    }

    @media screen and (min-width: 421px) {
      width: 320px;
    }

    background-color: #fff;
    border-radius: 8px;
    box-shadow: 7px 7px 13px 0px rgba(0, 0, 0, 0.38);

    font-weight: 500;
    font-size: 20px;
    line-height: 1.18;

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    .avatar {
      margin: 0 10px;
      width: 100px;
      height: 100px;

      object-fit: cover;

      border-radius: 8px;
      background-color: rgb(197, 197, 197);
    }
  }
}

.status {
  display: block;

  width: 20px;
  height: 20px;

  border-radius: 100%;
}

.online {
  composes: status;
  background-color: green;
}

.offline {
  composes: status;
  background-color: red;
}
